import React, { Component } from 'react'

//Component Imports
import TopBar from '../top_bar/top_bar';
import SideBar from '../side_bar/side_bar'
import MyEventsContent from './my_events_content'
import queryString from "query-string";
//Utils
import isMobile from '../../Utils/mobile_detector';
import ShowEvent from './show_event';


export default class MyEvents extends Component {

    constructor(props){
        super(props)
        const renderSideBarEvent = () =>{
            if(isMobile.any()){
                return false
            }
            else if(window.innerWidth<1900){
                return false
            }
            else{
                return true
            }
        }

        const renderSideBarEventList = () =>{
            if(isMobile.any()){
                return false
            }
            else{
                return true
            }
        }
       
        this.state={
            isSideBarOpenEvent:renderSideBarEvent(),
            isSideBarOpenEventList:renderSideBarEventList()
        }
    }

    componentDidMount=()=>{
    }

    toggleSideBarEvent = () =>{
        this.setState({
            isSideBarOpenEvent:!this.state.isSideBarOpenEvent
        })
    }
    toggleSideBarEventList = () =>{
        this.setState({
            isSideBarOpenEventList:!this.state.isSideBarOpenEventList
        })
    }
    
    render() {
        if(!queryString.parse(this.props.location.search).event){
            return (
                <div>
                    <TopBar toggleSideBar={this.toggleSideBarEventList} showSideBarToggle showAccountCirlce/>
                    <SideBar isOpen={this.state.isSideBarOpenEventList} activeMyEvents={true}/>
                    <MyEventsContent isSideBarOpen={this.state.isSideBarOpenEventList} isMobile={isMobile.any()}/>
                    
                </div>
            )
        }
        else{
          
            return(
            <div>
                <TopBar toggleSideBar={this.toggleSideBarEvent} showSideBarToggle showAccountCirlce/>
                <SideBar isOpen={this.state.isSideBarOpenEvent} activeMyEvents={true}/>
                <ShowEvent isSideBarOpen={this.state.isSideBarOpenEvent} event_id={queryString.parse(this.props.location.search).event}/>
            </div>
            )
                
            
        }
      
    }
}
