import React from 'react'
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import EventAvailableIcon from '@material-ui/icons/EventAvailable';
import SearchIcon from '@material-ui/icons/Search';
import TimelineIcon from '@material-ui/icons/Timeline';
import DashboardIcon from '@material-ui/icons/Dashboard';
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import SettingsIcon from '@material-ui/icons/Settings';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
}));


function SideBar(props) {
    const classes = useStyles();
    const theme = useTheme();
    const [open, setOpen] = React.useState(props.isOpen);
    const [activeDashboard, setActiveDashboard] = React.useState(props.activeDashboard);
    const [activeMyEvents, setActiveMyEvents] = React.useState(props.activeMyEvents);
    const [activeSearchEvent, setActiveSearchEvent] = React.useState(props.activeSearchEvent);
    const [activeTimeLine, setActiveTimeline] = React.useState(props.activeTimeline);
    const [activeMyPasses, setActiveMyPasses] = React.useState(props.activeMyPasses);
    const [activeMyProfile, setActiveMyProfile] = React.useState(props.activeMyProfile);
    const [activeSettings, setActiveSettings] = React.useState(props.activeSettings);
    
    React.useEffect(()=>{
        setOpen(props.isOpen);
        },
        [props.isOpen]
    )
    
    return (
        <div>
            <Drawer className={classes.drawer} variant="persistent" anchor="left" open={open} classes={{paper: classes.drawerPaper}}>
                
                <div className={classes.drawerHeader}>
                    <IconButton>
                        {theme.direction === 'ltr' ?
                        <ChevronLeftIcon /> :
                        <ChevronRightIcon />}
                    </IconButton>
                </div>

                <Divider />
 
                <List> 

                    <ListItem button key="dashboard" selected={activeDashboard | false} onClick={()=>window.location.href='/dashboard'}>
                        <ListItemIcon>
                          <DashboardIcon />
                        </ListItemIcon>
                        <ListItemText primary="Dashboard" />
                    </ListItem>

                    <ListItem button key="my_events" selected={activeMyEvents | false} onClick={()=>window.location.href='/my_events'}>
                        <ListItemIcon>
                          <EventAvailableIcon />
                        </ListItemIcon>
                        <ListItemText primary="My Events" />
                    </ListItem>

                    <ListItem button key="search_event" selected = {activeSearchEvent | false}>
                        <ListItemIcon>
                          <SearchIcon />
                        </ListItemIcon>
                        <ListItemText primary="Search Event" />
                    </ListItem>

                    <ListItem button key="timeline" selected = {activeTimeLine | false}>
                        <ListItemIcon>
                          <TimelineIcon />
                        </ListItemIcon>
                        <ListItemText primary="Timeline" />
                    </ListItem>

                    <ListItem button key="my_passes" selected = {activeMyPasses | false}>
                        <ListItemIcon>
                          <AssignmentIndIcon />
                        </ListItemIcon>
                        <ListItemText primary="My Passes" />
                    </ListItem>
                </List>
 
                <Divider />
 
                <List>
                  <ListItem button key="my_profile" selected = {activeMyProfile | false}>
                    <ListItemIcon>
                      <AccountCircleIcon />
                    </ListItemIcon>
                    <ListItemText primary="My Profile" />
                  </ListItem>

                  <ListItem button key="settings" selected = {activeSettings | false}>
                    <ListItemIcon>
                      <SettingsIcon />
                    </ListItemIcon>
                    <ListItemText primary="Settings" />
                  </ListItem>

                  <ListItem button key="logout" onClick={()=>{window.location.href = "/"}}>
                    <ListItemIcon>
                      <ExitToAppIcon />
                    </ListItemIcon>
                    <ListItemText primary="Logout" />
                  </ListItem>

                  
                </List>
           
            </Drawer>
        </div>
    )
}

export default SideBar;
