import React from 'react'
import {Container, Paper, Grid,Divider} from '@material-ui/core'
import { withStyles, makeStyles } from '@material-ui/core';
import theme from '../../themes/theme_selector';
import queryString from "query-string";
import Typography from '@material-ui/core/Typography';


//Imported Components
import Event from './event_template'
import VendorKiosk from './vendor_kiosk_template'

//Dev Imports
import DummyEvents from '../../DummyData/Events'
import DummyVendorKiosks from '../../DummyData/VendorKiosks'
import { Card,CardContent, CardActions,Button } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
    paper: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
        marginTop:theme.spacing(2),
        marginBottom:theme.spacing(2),
      },
      bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
      },
      title: {
        fontSize: 14,
      },
      pos: {
        marginBottom: 12,
      },
      button:{
        background:'#4caf50',
        color:'white',
        '&:hover':{
            background:'#388e3c'
        }
      },
}));


function ShowEvent(props) {
    const event_id = props.event_id
    const [isSideBarOpen, setIsSideBarOpen] = React.useState(props.isSideBarOpen)
    const event_data = DummyEvents.find((e)=> {
        return e.event_id == event_id;
    });
    const classes = useStyles();
    React.useEffect(()=>{
        setIsSideBarOpen(props.isSideBarOpen)
        },[props.isSideBarOpen]
    )
    const bull = <span className={classes.bullet}></span>;
    return (
        <div>
            <Container>
                    <Paper elevation = {3} className={classes.paper}>
                        
                        <Grid container spacing={2}>
                            <Grid item md={8} xs={12}>
                                <Paper elevation={3} style={{paddingBottom:theme.spacing(2)}}>
                                <img src={event_data.image} width="100%" style={{borderRadius:'8px'}}/>
                                <div style={{paddingLeft:theme.spacing(2), paddingRight:theme.spacing(2)}}>
                                    <h2 style={{marginBottom:theme.spacing(1)}}>{event_data.title}</h2>
                                    <Divider/>
                                    <Typography gutterBottom style={{fontSize:'0.9em', marginTop:theme.spacing(1)}} align="justify">
                                        <b>Description:</b> {event_data.description}
                                    </Typography>

                                    <Typography gutterBottom style={{fontSize:'0.9em', marginTop:theme.spacing(1)}} align="justify">
                                        <b>Date:</b> {event_data.date}
                                    </Typography>

                                    <h3  style={{marginBottom:theme.spacing(2)}}>Agenda Highlights:</h3>



                                    {
                                        event_data.event_agenda.map((agenda,index)=>{
                                            return(
                                                    <div style={{paddingBottom:theme.spacing(2)}}>
                                                        <Card className={classes.root} variant="outlined" >
                                                        <CardContent style={{paddingBottom:"0px"}}>
                                                        
                                                            <Typography variant="h6" component="h3">
                                                            {agenda.discussion_title}
                                                            </Typography>
                                                            <Typography color="textSecondary" style={{fontSize:'0.9em'}}>
                                                            <b>Time</b> : {agenda.discussion_time.start_time} - {agenda.discussion_time.end_time} {agenda.discussion_time.tz}
                                                            </Typography>
                                                            <Typography color="textSecondary"  className={classes.pos} style={{fontSize:'0.9em'}} >
                                                            <b>Speaker(s)</b> : {agenda.discussion_speaker}
                                                            </Typography>
                                                            <Typography variant="body2" component="p" align="justify">
                                                            <b>Description</b> : {agenda.discussion_description}
                                                            </Typography>
                                                        </CardContent>
                                                        <CardActions style={{display:'flex',justifyContent:'flex-end'}}>
                                                            <Button size="small" className={classes.button} style={{marginRight:theme.spacing(1), marginBottom:theme.spacing(1)}} onClick={()=>window.open(agenda.discussion_join_url,"_blank")}>Join</Button>
                                                        </CardActions>
                                                        </Card>
                                                        </div>
                                            )
                                        })
                                    }
                                    
                                 

                     
                                <center>
                                <Typography paragraph variant="overline" color="textSecondary">
                                      Copyright - 2020 - Powered by InnoMeet Events
                                  </Typography>
                                </center>

                                </div>
                                </Paper>
                                   
                            </Grid>
                            
                            <Grid item md={4} xs={12} >
                                <div style={{background:'lightgrey', paddingLeft:'10px', paddingRight:'10px', paddingTop:'1px', borderRadius:'7px', paddingBottom:theme.spacing(1)}}>
                                    <h3>Vendor Kiosk</h3>
                                    <Divider/>
                       
                                    {
                                        event_data.vendor_kiosks.map((vendorKiosk, index) =>
                                        {return(<VendorKiosk title={vendorKiosk.vendor_title} description={vendorKiosk.vendor_description} rooms={vendorKiosk.kiosk_rooms} key={index}/>)}
                                        )
                                    }
                                    
                                  
                      </div>
                            </Grid>
                    </Grid>
                    </Paper>
            </Container>
        </div>
    )
}

export default ShowEvent;
