import React from 'react'
import {AppBar, Toolbar, IconButton, Typography, Button, makeStyles, ThemeProvider, ListItem, Divider} from '@material-ui/core';
import {Menu,AccountCircle} from '@material-ui/icons'; //Import Menu to use Menu Icon
import theme from '../../themes/theme_selector'
import Popover from '@material-ui/core/Popover';
import EditIcon from '@material-ui/icons/Edit';
import SettingsApplicationsIcon from '@material-ui/icons/SettingsApplications';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';

const useStyles = makeStyles((theme) => ({
  Logo: {
    flex: 1
  },
  HeaderButton: {
    marginLeft: '10px'
  },
  alwaysOnTop: {
    zIndex: theme.zIndex.drawer + 1,
  },
  typography: {
    padding: theme.spacing(1),
  },
  listItem: {
    padding: theme.spacing(1),
    display: "inline-block",
    verticalAlign: "middle",
    lineHeight: "normal",
  },
  button: {
    textTransform: "none",
    justifyContent: 'left'
  }
}));



function TopBar(props) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const showRegisterButton = props.showRegisterButton | false;
  const showLoginButton = props.showLoginButton | false;
  const showSideBarToggle = props.showSideBarToggle | false;
  const showAccountCirlce = props.showAccountCirlce | false;

  const openAccountCirclePopOver = Boolean(anchorEl);
  const idAccountCirlce = openAccountCirclePopOver ? 'simple-popover' : undefined;

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
    
    return (
        <ThemeProvider theme={theme}>
          <AppBar position="sticky" color="primary" className={classes.alwaysOnTop}>
            <Toolbar>
              <IconButton edge="start" color="inherit" aria-label="menu" onClick={(e)=>props.toggleSideBar(e)} style={!showSideBarToggle?{display:'none'}:{}}>
                <Menu />
              </IconButton>
              <Typography variant="h6" className={classes.Logo}>
                InnoMeet Events
              </Typography>
              <Button color="inherit" className={classes.HeaderButton} style={!showLoginButton?{display:'none'}:{}} onClick={props.showLoginDialogToggle}>Login</Button>
              <Button color="inherit" className={classes.HeaderButton}style={!showRegisterButton?{display:'none'}:{}}>Register</Button>
              <IconButton edge="start" color="inherit" aria-label="menu" className={classes.HeaderButton} style={!showAccountCirlce?{display:'none'}:{}} aria-describedby={idAccountCirlce} onClick={handleClick}>
                <AccountCircle />
              </IconButton>
              <Popover
                id={idAccountCirlce}
                open={openAccountCirclePopOver}
                anchorEl={anchorEl}
                onClose={handleClose}
                anchorOrigin={{
                  vertical: 'bottom',
                  horizontal: 'center',
                }}
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'center',
                }}
              >
                <Typography className={classes.typography}>
                 
                <Button
                  color="primary"
                  className={classes.button}
                  startIcon={<EditIcon fontSize='small'/>}
                  size="small"
                  fullWidth={true}
                >
                  Edit
                </Button>
             
                  <Divider />
             
                  <Button
                  color="primary"
                  className={classes.button}
                  startIcon={<SettingsApplicationsIcon/>}
                  size="small"
                  fullWidth={true}
                >
                  Settings
                </Button>
             
                  <Divider />
            
                  <Button
                  color="primary"
                  className={classes.button}
                  startIcon={<ExitToAppIcon/>}
                  size="small"
                  fullWidth={true}
                >
                  Logout
                </Button>
            
                </Typography>
              </Popover>
            </Toolbar>
          </AppBar>
        </ThemeProvider>
        
    )
}
export default TopBar;
