import React from 'react'
import {Container, Paper, Grid,Divider} from '@material-ui/core'
import { withStyles, makeStyles } from '@material-ui/core';
import theme from '../../themes/theme_selector';

//Imported Components
import Event from './event_template'
import VendorKiosk from './vendor_kiosk_template'

//Dev Imports
import DummyEvents from '../../DummyData/Events'
import DummyVendorKiosks from '../../DummyData/VendorKiosks'


const useStyles = makeStyles((theme) => ({
    paper: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(2),
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2),
        marginTop:theme.spacing(2),
        marginBottom:theme.spacing(2),
      },
}));


function MyEventsContent(props) {
    const [isSideBarOpen, setIsSideBarOpen] = React.useState(props.isSideBarOpen)

    const classes = useStyles();
    React.useEffect(()=>{
        setIsSideBarOpen(props.isSideBarOpen)
        },[props.isSideBarOpen]
    )
    
    return (
        <div style={(isSideBarOpen && !props.isMobile)?{paddingLeft:'240px',transition: 'all .2s linear'}:{}}>
            <Container> 
                    <Paper elevation = {3} className={classes.paper}>
                        <h2>My Events</h2>
                        <hr/>
                        <Grid container spacing={2}>
                            <Grid item md={12} xs={12}>
                                
                                    <h3>Planned Events</h3>
                                    <Divider/>
                                    {
                                        DummyEvents.map((event, index) =>
                                        {return(<Event image={event.image} title={event.title} description={event.description} join_url={event.join_url} breaks={event.breaks} notes={event.notes} event_id={event.event_id} key={index}/>)}
                                        )
                                    }
                            </Grid>
                            
                            {/* <Grid item md={4} xs={12} >
                                <div style={{background:'lightgrey', paddingLeft:'10px', paddingRight:'10px', paddingTop:'1px', borderRadius:'7px', paddingBottom:theme.spacing(1)}}>
                                    <h3>Vendor Kiosk</h3>
                                    <Divider/>
                       
                                    {
                                        DummyVendorKiosks.map((vendorKiosk, index) =>
                                        {return(<VendorKiosk title={vendorKiosk.title} description={vendorKiosk.description} rooms={vendorKiosk.rooms} key={index}/>)}
                                        )
                                    }
                                    
                                  
                      </div>
                            </Grid> */}
                    </Grid>
                    </Paper>
            </Container>
        </div>
    )
}

export default MyEventsContent;
