import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { ThemeProvider, makeStyles, Input, } from '@material-ui/core';
import theme from '../../themes/theme_selector'

const useStyles = makeStyles((theme) => ({
    
    button: {
      textTransform: "none",
      justifyContent: 'left'
    },

    dialog:{
        paddingRight:theme.spacing(3),
        paddingBottom:theme.spacing(3)
    }
}));



export default function LoginDialog(props) {
  const [open, setOpen] = React.useState(props.showLoginDialog);
  const [email, setEmail] = React.useState("");
  const [password, setPassword] = React.useState("");
  const classes = useStyles();

  React.useEffect(()=>{
        setOpen(props.showLoginDialog);
        },[props.showLoginDialog]
  )

  const handleClose = () => {
    props.showLoginDialogToggle()
  };

  const handleSubmit=(e)=>{
      e.preventDefault();
      window.location.href="/dashboard"
  }

  return (
    <ThemeProvider theme={theme}>
        
    
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title" >
        <center><DialogTitle id="form-dialog-title" style={{paddingBottom:"0px"}}>Login to InnoMeet Events!<hr/></DialogTitle></center>
        
        <DialogContent>
        
            <center><img src="/images/logo_transparent.png" height="192px" width="192px" style={{paddingLeft:'50px', paddingRight:'50px'}}/></center>
        </DialogContent>
        <form onSubmit={e=>handleSubmit(e)}>
            <DialogContent>
                <Input placeholder="Email ID" type="email" inputProps={{style:{paddingLeft:'10px', paddingRight:'10px', textAlign:'center'}}} onChange={e=> setEmail(e.target.value)} fullWidth/>
            </DialogContent>

            <DialogContent>
                <Input placeholder="Password" type="password" inputProps={{style:{paddingLeft:'10px', paddingRight:'10px', textAlign:'center'}}} onChange={e=> setPassword(e.target.value)} fullWidth/>
            </DialogContent>

            <DialogActions className={classes.dialog}>
            <Button onClick={handleClose}  color="secondary" className={classes.button}>
                Cancel
            </Button>
            <Button variant="contained" color="primary" className={classes.button} type="submit">
                Login
            </Button>
            </DialogActions>
        </form>
      </Dialog>
    </ThemeProvider>
  );
}