import React from 'react';
import { makeStyles, ThemeProvider } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import theme from '../../themes/theme_selector'
import { Divider, Grid, Paper,ButtonBase } from '@material-ui/core';
import { Room } from '@material-ui/icons';

const useStyles = makeStyles({
  root: {
    marginTop:theme.spacing(2),
    marginBottom:theme.spacing(2)
  },
  media: {
    height: 140,
  },
  button:{
    background:'#4caf50',
    color:'white',
    '&:hover':{
        background:'#388e3c'
    }
  },
  rooter: {
    flexGrow: 1,
  },
  paper: {
    marginBottom:'10px',
    marginLeft:"10px",
    marginRight:"10px",
    margin: 'auto',
    // maxWidth: 500,
    boxShadow:'none',
    border:"2px solid darkgrey",
    borderRadius:0,
    cursor:"pointer",
    "&:hover":{
        background: "#EDEDED"
    }
    
  },
  image: {
    width: 48,
    height: 48,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  actionArea:{
    cursor:"default"
  }

  
});



export default function VendorKiosk(props) {

 

  const cardTitle = props.title
  const cardDescription = props.description;
  const rooms = props.rooms;
 

  const classes = useStyles();

  return (
    <ThemeProvider theme = {theme}>
        <Card className={classes.root}>
            <CardActionArea className={classes.actionArea}>
                
                <CardContent style={{paddingBottom:'4px'}}>
                <Typography gutterBottom variant="h6" component="h2">
                    {cardTitle}
                </Typography>
                <hr/>
                <Typography variant="body2" color="textSecondary" component="p">
                    {cardDescription}
                </Typography>
                </CardContent>
            </CardActionArea>
            <CardActions>
            <div className={classes.rooter}>
            {
              rooms.map((room, index) =>{
                return(
<Paper className={classes.paper} key={index} onClick={()=>window.open(room.join_url, "_blank")}>
        <Grid container xs={12} spacing={1}>
          <Grid item>
            <ButtonBase className={classes.image}>
              <img className={classes.img} alt="complex" src={room.kiosk_room_icon}/>
            </ButtonBase>
          </Grid>
          <Grid item xs={9} container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs style={{display: 'flex',alignItems: 'center'}}>
                <Typography gutterBottom variant="subtitle1" style={{margin:"0px", fontSize:'0.9em', lineHeight:"1.4em"}} >
                  {room.kiosk_room_name}
                </Typography>
                
              </Grid>
              
            </Grid>
            
          </Grid>
        </Grid>
      </Paper>
              
              )}) }
      
    </div>

    
            </CardActions>
            </Card>
    </ThemeProvider>
    
  );
}