import React, { Component } from 'react';

//Component Imports
import TopBar from '../top_bar/top_bar';
import SideBar from '../side_bar/side_bar'
import DashboardContent from './dashboard_content'
import useMediaQuery from '@material-ui/core/useMediaQuery';

//Utils
import isMobile from '../../Utils/mobile_detector';


export default class Dashboard extends Component {
    constructor(props){
      
        super(props)
        const renderSideBar=()=>{
            if(isMobile.any()){
                return false
            }
            else{
                return true
            }
        }
        this.state={
            isSideBarOpen:renderSideBar()
        }
    }


    toggleSideBar = () =>{
        
        this.setState({
            isSideBarOpen:!this.state.isSideBarOpen
        })
    }

    render() {
        return (
            <div>
                <TopBar toggleSideBar={this.toggleSideBar} showSideBarToggle showAccountCirlce/>
                <SideBar isOpen={this.state.isSideBarOpen} activeDashboard={true}/>
                <DashboardContent isSideBarOpen={this.state.isSideBarOpen} isMobile={isMobile.any()}/>
            </div>
        )
    }
}
