import React from 'react';
import { makeStyles, ThemeProvider } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import Image from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import theme from '../../themes/theme_selector'
import truncateString from '../../Utils/truncate_string'
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { withStyles } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';


const useStyles = makeStyles({
  root: {
    marginTop:theme.spacing(2),
    marginBottom:theme.spacing(2)
  },
  media: {
    height: 240,
  },
  
  button:{
    background:'#4caf50',
    color:'white',
    '&:hover':{
        background:'#388e3c'
    }
  },
});

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);



export default function Event(props) {
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));

  const cardImage = props.image;
  const cardTitle = props.title;
  const event_id = props.event_id
  const cardDescription = props.description
  const join_url = props.join_url;
  const breaks = props.breaks;
  const notes = props.notes;

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const classes = useStyles();

  return (
    <ThemeProvider theme={theme}>
      <Card className={classes.root}>
        <CardActionArea>
          <CardMedia className={classes.media} image={cardImage} title={cardTitle} />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              {cardTitle}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              {truncateString(cardDescription,500)}
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions
          style={{display:"flex",justifyContent:'flex-end', marginRight:theme.spacing(1), marginBottom:theme.spacing(1)}}>
          {/* <Button size="small" color="primary" onClick={handleClickOpen}>
            Learn More
          </Button> */}
          <Button size="small" variant="contained" className={classes.button} onClick={()=>window.location.href="/my_events?event="+event_id}>
            Go To Event
          </Button>
        </CardActions>
      </Card>


      {/* <div >
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open} fullScreen={fullScreen} >
        <DialogTitle id="customized-dialog-title" onClose={handleClose} >
          {cardTitle}
        </DialogTitle>
        <DialogContent dividers>
        <img src={cardImage} style={{width:"100%", borderRadius:"5px"}}/>
          <Typography gutterBottom style={{fontSize:'0.9em', padding:theme.spacing(1)}} align="justify">
            <b>About</b> : {cardDescription}
          </Typography>
          <Typography gutterBottom style={{fontSize:'0.9em', padding:theme.spacing(1)}} align="justify">
            <b>Breaks</b> : 
                {
                  breaks?<ul style={{marginTop:"0px",marginBottom:"0px", padding:"0px"}}>{breaks.map((break_obj,index)=>{return(<ul>{break_obj.from} to {break_obj.to} {break_obj.tz}</ul>)})}</ul>:" No breaks!"
                }                 
          </Typography>
          <Typography gutterBottom style={{fontSize:'0.9em', padding:theme.spacing(1)}} align="justify">
            <b>Notes</b> : {notes?notes:"none."}  
          
          </Typography>
        </DialogContent>
        <DialogActions style={{ marginRight:theme.spacing(1), marginBottom:theme.spacing(1)}}>
          <Button onClick={handleClose} color="primary">
            Close
          </Button>
          <Button size="small" variant="contained" className={classes.button} onClick={()=>window.open(join_url,
            "_blank")}>
            JOIN
          </Button>
        </DialogActions>
      </Dialog>
      </div> */}

    </ThemeProvider>

  );
}