import React from 'react'
import {Container, Paper, Typography} from '@material-ui/core'
import { withStyles, makeStyles, Divider } from '@material-ui/core';
import theme from '../../themes/theme_selector'

const useStyles = makeStyles((theme) => ({
    paper: {
        padding: theme.spacing(2),
        marginTop:theme.spacing(2),
        marginBottom:theme.spacing(2),
      },
}));
function DashboardContent(props) {
    const classes = useStyles();
    const [isSideBarOpen,setIsSideBarOpen] = React.useState(props.isSideBarOpen);
    React.useEffect(()=>{
        setIsSideBarOpen(props.isSideBarOpen)
        },[props.isSideBarOpen]
    )
    
    return (
        <div style={(isSideBarOpen && !props.isMobile)?{paddingLeft:'240px',transition: 'all .2s linear'}:{}}>
            <Container> 
                    <Paper elevation = {3} className={classes.paper}>
                        <center><h1>Welcome to InnoMeet Events!</h1></center>
                        <Divider/>
                        <img src="https://www.jotform.com/blog/wp-content/uploads/2020/03/How-to-record-a-webinar_image.svg" width="100%" style={{paddingTop:theme.spacing(2)}}></img>
                        <center>
                            <Typography style={{paddingTop:theme.spacing(2)}}>
                                Please explore InnoMeet Events by clicking "My Events" on the Sidebar...
                            </Typography>
                        </center>
                    </Paper>
                    
            </Container>
        </div>
    )
}

export default DashboardContent;
