//React Imports
import React, { Component } from 'react';

//Component Imports
import TopBar from '../top_bar/top_bar';
import LandingPageContent from './landing_page_content';
import LoginDialog from './login_dialog';
// import SideBar from '../side_bar/side_bar'

export default class LandingPage extends Component {

    constructor(props){
        super(props)
        this.state={
            showLoginDialog:false,
            enableTests:false

        }
    }

    // toggleSideBar = () =>{
        
    //     this.setState({
    //         isSideBarOpen:!this.state.isSideBarOpen
    //     })
    // }

    showLoginDialogToggle=()=>{
        this.setState({
            showLoginDialog:!this.state.showLoginDialog
        })
    }

    render() {
        return (
            <div>
                <TopBar showLoginButton showRegisterButton showLoginDialogToggle={this.showLoginDialogToggle}/>
                <LoginDialog showLoginDialog={this.state.showLoginDialog} showLoginDialogToggle={this.showLoginDialogToggle}></LoginDialog>
                {/* <SideBar isOpen={this.state.isSideBarOpen}/> */}
                <LandingPageContent/>
                
                
            </div>
        )
    }
}
